@import url(https://fonts.googleapis.com/css?family=Montserrat);
body{
  background: url(/Hotline-News/static/media/bg.64904d5e.jpg)
  no-repeat center center fixed;
  background-size: cover;
}



  a {
    text-decoration: none;
    color: rgb(255, 251, 0);
  }
  a:hover {
    text-decoration: underline;
  }
  
  .card {
    display: inline-block;
    border-radius: 4px;
    padding: 10px;
    overflow: hidden;
    cursor: pointer;
    margin-bottom: 2rem;  
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
    background-color: rgb(0,0,0); 
    background-color: rgba(0,0,0, 0.4); 
  }

  .card:hover{
    transition: -webkit-transform .4s ease;
    transition: transform .4s ease;
    transition: transform .4s ease, -webkit-transform .4s ease;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    background-color: rgba(34, 34, 34, 0.4);
    box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
}
  
  .cover {
    position: relative;
    margin-bottom: 18px;
  }
  .cover img {
    display: block;
    width: 100%;
  }
  .cover.artist img {
    border-radius: 50%;
  }
  
  .play-icon {
    position: absolute;
    bottom: -2px;
    right: -4px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    color: #fafafa;
    font-size: xxx-large;
    opacity: 0;
    transition: 0.4s, -webkit-transform 0.1s;
    transition: 0.4s, transform 0.1s;
    transition: 0.4s, transform 0.1s, -webkit-transform 0.1s;
    bottom: -123px;
  }
  .play-icon:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  .card:hover .play-icon {
    bottom: 6px;
    opacity: 1;
  }
  
  .fa-play {
    position: absolute;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    left: 52%;
    top: 50%;
    font-size: 1rem;
  }
  
  .card-content {
    font-family: "Montserrat", sans-serif;
    color: #fafafa;
  }
  
  .card-content p {
    font-size: 14px;
    margin-bottom: 0;
  }
  
  .card-content small{
    font-size: 11px;
    color: #000000;


  }
.mybox {
  font-family: Montserrat, sans-serif;

  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

* {
  box-sizing: border-box;
}

body {
  background-color: #28223f;
  font-family: Montserrat, sans-serif;

  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  min-height: 100vh;
  margin: 0;
}

h3 {
  margin: 10px 0;
}

h6 {
  margin: 5px 0;
  text-transform: uppercase;
}

p {
  font-size: 14px;
  line-height: 21px;
}

.card-container {
  background-color: #231e39;
  border-radius: 5px;
  box-shadow: 0px 10px 20px -10px rgba(0, 0, 0, 0.75);
  color: #b3b8cd;
  padding-top: 30px;
  padding-bottom: 30px;
  position: relative;
  width: 350px;
  max-width: 100%;
  text-align: center;
}

.card-container .pro {
  color: #231e39;
  background-color: #febb0b;
  border-radius: 3px;
  font-size: 14px;
  font-weight: bold;
  padding: 3px 7px;
  position: absolute;
  top: 30px;
  left: 30px;
}

.card-container .round {
  border: 1px solid #03bfcb;
  border-radius: 50%;
  padding: 7px;
  height: 180px;
}


.social-icons {
    padding: 0;
    list-style: none;
    margin: 1em;
  }
  
  .social-icons li {
    display: inline-block;
    margin: 0.15em;
    position: relative;
    font-size: 1em;
  }

  .social-icons svg {
    color: #fff;
    position: absolute;
    top: 0.95em;
    left: 0.96em;
    transition: all 265ms ease-out;
  }
  

  .social-icons a {
    display: inline-block;
  }
  
  .social-icons a:before {
    transform: scale(1);
    -ms-transform: scale(1);
    -webkit-transform: scale(1);
    content: " ";
    width: 45px;
    height: 45px;
    border-radius: 100%;
    display: block;
    background: linear-gradient(45deg, #ff003c, #c648c8);
    transition: all 265ms ease-out;
  }
  
  .social-icons a:hover:before {
    -webkit-transform: scale(0);
            transform: scale(0);
    transition: all 265ms ease-in;
  }

  .social-icons a:hover svg {
    transform: scale(2.2);
    -ms-transform: scale(2.2);
    -webkit-transform: scale(2.2);
    color: #ff003c;
    background: -webkit-linear-gradient(45deg, #ff003c, #c648c8);
    -webkit-background-clip: text;
            background-clip: text;
    -webkit-text-fill-color: transparent;
    transition: all 265ms ease-in;
  }
  
 
